import React, { useRef, useState } from "react";
import styles from "./css/contactSlide.module.css";

import emailjs from "@emailjs/browser";
import { useSwiperSlide } from "swiper/react";

function ContactSlide() {
  const swiperSlide = useSwiperSlide();

  // e-mail validation check
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_qdo2opg",
        "template_cqw78qq",
        form.current,
        "esQPuyyAvqcchgZ7X"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );

    setMailContent({
      name: "",
      email: "",
      phone: "",
      message: "",
    });

    setContactMeModal(!contactMeModal);

    setChecked(false);

  };

  const [mailContent, setMailContent] = useState({
    name: "",
    email: "",
    phone: "",
    message: "",
  });

  const { name, email, phone, message } = mailContent;

  const onChangeInput = (e) => {
    const { name, value } = e.target;
    setMailContent({ ...mailContent, [name]: value });
  };

  const [checked, setChecked] = useState(false);

  const isValidEmail = email.includes("@") && email.includes(".");

  const isValidInput =
    name.length >= 1 && email.length >= 1 && phone.length >= 1;

  const getIsActive = isValidEmail && isValidInput && checked === true;

  const handleButtonValid = () => {
    if (!isValidEmail || !isValidInput || !checked) {
      alert("   , .");
    }
  };

  //contactMeModal

  const [contactMeModal, setContactMeModal] = useState(false);

  return (
    <section className={`${styles.secBg}`}>
      <div className={`${styles.contactSlideCon}`}>
        <h2
          className={
            swiperSlide.isActive
              ? `${styles.contactTitle} ${styles.fadeIn}`
              : `${styles.contactTitle}`
          }
        >
                
          <br />
          <strong> / </strong>  ?
        </h2>
        <div className={`${styles.contactCon}`}>
          <div
            className={
              swiperSlide.isActive
                ? `${styles.contactTxt}  ${styles.fadeLeft}`
                : `${styles.contactTxt}`
            }
          >
            <p>
                     
              . <br />
                     <br />
                '(HOW)'   <br />
              '(WHY)', '(WHAT)', '(WHO)', '(WHEN)' <br />
                     
              .
            </p>
            <span>
                  <strong> </strong>{" "}
              
            </span>
            <div className={`${styles.contactLinkCon}`}>
              <div
                className={`${styles.contactLinkBtn}`}
                onClick={() =>
                  window.open("https://open.kakao.com/o/swu4Adwe/", "_blank")
                }
              >
                <img
                  src={`${process.env.PUBLIC_URL}/img/icons/kakao.png`}
                  alt="kakao"
                />
                <span>colleksql</span>
              </div>
              <div
                className={`${styles.contactLinkBtn}`}
                onClick={() =>
                  window.open("https://dana-contact.slack.com/", "_blank")
                }
              >
                <img
                  src={`${process.env.PUBLIC_URL}/img/icons/slack.png`}
                  alt="slack"
                />
                <span>colleksql3@gmail.com</span>
              </div>
            </div>
          </div>
          <div
            className={
              swiperSlide.isActive
                ? `${styles.contactMe} ${styles.fadeRight}`
                : `${styles.contactMe}`
            }
          >
            <div className={`${styles.emailForm}`}>
              <h3>send to e-mail</h3>
              <form ref={form} onSubmit={sendEmail}>
                <div className={`${styles.name}`}>
                  <label></label>
                  <input
                    type="text"
                    name="name"
                    placeholder=" "
                    autoComplete="off"
                    value={name}
                    onChange={onChangeInput}
                  />
                </div>
                <div className={`${styles.email}`}>
                  <label>Email</label>
                  <input
                    type="email"
                    name="email"
                    placeholder="  "
                    autoComplete="off"
                    value={email}
                    onChange={onChangeInput}
                  />
                </div>
                <div className={`${styles.phone}`}>
                  <label></label>
                  <input
                    type="text"
                    name="phone"
                    placeholder=" "
                    autoComplete="off"
                    value={phone}
                    onChange={onChangeInput}
                  />
                </div>
                <div className={`${styles.message}`}>
                  <label> </label>
                  <textarea
                    name="message"
                    placeholder=" "
                    value={message}
                    onChange={onChangeInput}
                  />
                </div>
                <div className={`${styles.agree}`}>
                  <input id="checkAgree" type="checkbox" checked={checked} onChange={(e) => setChecked(e.target.checked)} />
                  <label htmlFor="checkAgree">
                    <span className={`${styles.agreeSign}`}><strong></strong></span>
                    <p>
                        ,  
                      .   .
                  </p>
                  </label>
                </div>
                <div className={`${styles.submit}`}>
                  <input
                    disabled={
                      isValidEmail && isValidInput && checked
                        ? false
                        : true
                    }
                    type="submit"
                    value="Send"
                    onClick={handleButtonValid}
                    className={
                      getIsActive
                        ? `${styles.submitBtnAction} ${styles.submitBtn}`
                        : `${styles.submitBtnInaction} ${styles.submitBtn}`
                    }
                  />
                </div>
              </form>
            </div>
          </div>
        </div>
        {contactMeModal === true ? (
          <ContactMeModal
            contactMeModal={contactMeModal}
            setContactMeModal={setContactMeModal}
          />
        ) : null}
      </div>
    </section>
  );
}

function ContactMeModal(props) {
  return (
    <>
      <div className={`${styles.contactMeModalBg}`}></div>
      <div className={`${styles.contactMeModal}`}>
        <p>
          <span>  .</span>
          <span>  .</span>
          <span>:&#41;</span>
        </p>
        <button
          className={`${styles.closeBtn}`}
          onClick={() => {
            props.setContactMeModal(!props.contactMeModal);
          }}
        >
          <span></span>
          <span></span>
        </button>
      </div>
    </>
  );
}

export default ContactSlide;
